#player {
  width: 400px;
  display: flex;
  margin: 0 auto;
  border: 1px solid black;
  border-radius: 10px;
  outline: none;
}

#player > * {
  margin: 0;
}

#player > *:hover {
  box-shadow: inset 1px 1px 3px rgb(51, 49, 49);
  cursor: pointer;
}

.button-type {
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid black;
}

#play-button {
  border-radius: 10px 0px 0px 10px;
}

.progress-bar {
  width: 70%;
  position: relative;
  border-radius: 0px 10px 10px 0px;
}

.progress-bar p {
  margin-left: 5px;
}

#progress-amount {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(121, 183, 220, 0.8);
  height: 100%;
  z-index: -1;
  border-radius: 0px 10px 10px 0px;
}
